body{margin:0px;font-family:'Open Sans', helvetica, sans-serif;line-height:25px;color:#000a12;font-size:16px}body .embed-show{display:none}h1{font-size:48px;font-weight:800}h4{font-size:21px;margin-bottom:20px}h5{font-weight:400;font-size:15px;padding-left:5px;font-style:italic;text-align:right;margin:5px auto 0px auto;max-width:650px}section,header,footer{padding:30px 10px;max-width:650px;margin:0px auto}.center-offset{max-width:650px;margin:0px auto;padding:0px 10px}header{padding-top:10px}header .lang-picker{text-align:right}header .lang-picker a{text-decoration:none}a,a:hover,a:visited{color:#000a12}h1{font-size:24px}.text-center{text-align:center}#kpi{padding-top:20px;display:flex;flex-wrap:wrap;justify-content:space-between;text-align:center}#kpi>div{width:30%;margin-bottom:35px}#kpi>div .diff{margin-bottom:8px;font-weight:700}#kpi>div .value{font-size:32px;font-weight:800;margin-bottom:14px}#kpi>div .label{color:#000a12;font-weight:700}#kpi #kpi-confirmed{color:#f44334}#kpi #kpi-recovered{color:#1876d3}#kpi #kpi-deceased{color:#36484f}#kpi #kpi-critical{color:#720405}#kpi #kpi-tested{color:#a4adc0}#kpi #kpi-active{color:#df0e1f}#kpi table{text-align:center;width:100%;border-spacing:0px 10px}#kpi .kpi-metrics{font-size:28px;font-weight:800}#kpi .kpi-labels{font-size:18px;font-weight:700}#map-container{width:100%;height:400px;background:#cad2d3;max-width:650px;margin:0px auto;border-radius:6px}#map-legend{padding-top:0px;padding-bottom:10px;font-size:16px;display:flex;flex-wrap:wrap;justify-content:space-between}#map-legend>div{display:inline-block}#map-legend span.one{color:#fdeacb}#map-legend span.two{color:#fb9b7f}#map-legend span.three{color:#f44336}#map-legend span.four{color:#ba000d}#trend-chart-container path.c3-line,#daily-increase-container path.c3-line{stroke-width:2px}#trend-chart-container line.c3-xgrid,#trend-chart-container line.c3-ygrid,#daily-increase-container line.c3-xgrid,#daily-increase-container line.c3-ygrid{stroke-dasharray:0 0;stroke:#f0f0f0 !important}#prefectures-table{width:100%;border-collapse:separate;border-spacing:0px;font-size:14px}#prefectures-table tr:nth-child(even){background-color:#f4f7f8}#prefectures-table th{text-align:left;font-size:12px}#prefectures-table td{padding:5px 0px}#prefectures-table td:first-child{padding:5px}#prefectures-table .totals{font-weight:bold}#prefectures-table .loading td{text-align:center;padding:30px 0px}#helpful-links li{margin-bottom:20px;word-wrap:break-word}footer{line-height:34px}footer a,footer a:active{color:#1976d2 !important}.lds-dual-ring{display:inline-block;width:24px;height:24px}.lds-dual-ring:after{content:" ";display:block;width:16px;height:16px;border-radius:50%;border:4px solid #f44336;border-color:#f44336 transparent #f44336 transparent;animation:lds-dual-ring 1.6s linear infinite}@keyframes lds-dual-ring{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}body.embed .embed-hide{display:none}body.embed .embed-show{display:block}body.embed span.embed-show{display:inline}
